<template>
  <div
    class="horizontal-calendar"
    v-bind:style="{'border-top':showBorderTop?'1px solid #f2f2f2':'none'}"
  >
    <span class="left-arrow" v-on:click="dateFlip(-1)">
      <slot name="leftIcon" v-if="this.$slots['leftIcon']"></slot>
      <svg v-else class="icon" aria-hidden="true">
        <use xlink:href="#jac-left" />
      </svg>
    </span>
    <div class="date-list">
      <div
        class="date-list-scroll"
        :style="{'transform': 'translateX('+translateX+'px)','transition-duration': transitionDuration}"
      >
        <div
          class="date-item"
          v-for="(day,index) in dateList"
          v-bind:key="index"
          v-on:click="changeChoosedDay(day)"
          v-bind:class="{'choosed-day': day.dateFormat == choosedDay.dateFormat,'today':day.dateFormat == today.dateFormat && lang == 'zh'}"
          v-bind:style="{'background-color': day.dateFormat == choosedDay.dateFormat?choosedItemColor:day.dateFormat == today.dateFormat?todayItemColor:''}"
        >
          <div>
            <p class="date-item-day">{{day.day}}</p>
            <p
              class="date-item-date"
              v-if="day.dateFormat == today.dateFormat && lang == 'zh'"
              v-bind:style="{'color': day.dateFormat == choosedDay.dateFormat?'#fff':day.dateFormat == today.dateFormat?choosedItemColor:''}"
            ></p>
            <p class="date-item-date" v-else>{{day.date}}</p>
          </div>
          <div class="first-day" v-if="day.date == 1">
            <p>{{day.month}}</p>
          </div>
        </div>
      </div>
    </div>
    <span class="right-arrow" v-on:click="dateFlip(1)">
      <slot name="rightIcon" v-if="this.$slots['rightIcon']"></slot>
      <svg v-else class="icon" aria-hidden="true">
        <use xlink:href="#jac-right" />
      </svg>
    </span>
  </div>
</template>
<script>
import "@/assets/iconfont.js";

export default {
  name: "vue-horizontal-calendar",
  props: {
    // ,   2019/12/01  2019-12-01 UTC
    choosedDate: {
      type: [String, Date],
      required: false,
      default: () => {
        return new Date();
      }
    },
    //
    swipeSpace: {
      type: [String, Number],
      required: false,
      default: 7
    },
    // 'leftcenter'
    choosedDatePos: {
      type: String,
      required: false,
      default: "left"
    },
    //  2019/12/01  2019-12-01  UTC
    minDate: {
      type: [String, Date],
      required: false,
      default: ""
    },
    //  2019/12/01  2019-12-01  UTC
    maxDate: {
      type: [String, Date],
      required: false,
      default: ""
    },
    // 
    choosedItemColor: {
      type: String,
      required: false,
      default: "rgb(13, 141, 224)"
    },
    // 
    todayItemColor: {
      type: String,
      required: false,
      default: "rgba(13, 141, 224,.1)"
    },
    // 
    sundayText: {
      type: String,
      required: false,
      default: ""
    },
    // 
    showBorderTop: {
      type: Boolean,
      required: false,
      default: true
    },
    // 
    resizeable: {
      type: Boolean,
      required: false,
      default: true
    },
    // zh()en()
    lang: {
      type: String,
      required: false,
      default: "zh"
    }
  },
  data() {
    return {
      // 
      // {
      //   dateFormat: "2019/07/20",
      //   year: "2019",
      //   month: "07",
      //   date: "20",
      //   timestamp: 1564046915797,
      //   day: ""
      // }

      firstDay: {}, //
      visibleDay: "", // 
      changeCount: Number(this.swipeSpace), //7
      choosedDay: {}, // 
      dateList: [], // 
      today: {}, // 

      // 
      translateX: -this.changeCount * 50,
      transitionDuration: "300ms",
      domWidth: 0, // 

      swipeLeftMore: true, // 
      swipeRightMore: true // 
    };
  },
  mounted() {
    this.init();
    // change
    this.$emit("change", this.choosedDay);

    /**
     * @@author jacques
     * @@date 2019.12.06
     * @@desc 
     * onresizena,b
     * */
    if (this.resizeable) {
      let a = 1;
      window.onresize = () => {
        a++;
        let b = a;
        setTimeout(() => {
          if (b == a) {
            this.init();
          }
        }, 200);
      };
    }
  },
  methods: {
    init() {
      // 
      this.domWidth = this.$el.scrollWidth;
      let n = (this.domWidth - 60) / 50;
      //  = +1 div50
      if (n % 1 != 0) {
        n = parseInt(n) + 1;
      }
      this.visibleDay = n;

      // ""
      this.today = this.formatOneDay(new Date());
      // ""
      this.choosedDay = this.formatOneDay(this.choosedDate);

      // 
      let firstDay = this.formatOneDay(this.choosedDate);
      if (this.choosedDatePos === "center") {
        let ts =
          firstDay.timestamp -
          parseInt(this.visibleDay / 2) * 1000 * 60 * 60 * 24;
        this.firstDay = this.formatOneDay(ts);
      } else {
        this.firstDay = firstDay;
      }
      this.creatList();

      // : 
      this.$emit("firstDayChange", this.firstDay);
    },
    // 
    creatList() {
      let list = [];
      let firstDayTime = this.firstDay.timestamp;
      for (let i = 0; i < this.visibleDay; i++) {
        let timestamp = firstDayTime + 1000 * 60 * 60 * 24 * i; //+24*i
        let info = this.formatOneDay(new Date(timestamp));
        list.push(info);
      }
      this.dateList = list;
      this.translateX = 0;

      // 
      this.swipeLeftMore = true;
      this.swipeRightMore = true;
    },
    // 
    changeChoosedDay(day) {
      this.choosedDay = day;
      this.$emit("change", day);
    },
    //  17-17
    dateFlip(type) {
      // 
      let beforeChangeX = this.translateX;
      let beforeChangeLen = this.dateList.length;

      // 
      if (type === -1) {
        // 1translateX< 
        if (this.translateX <= -50 * this.changeCount) {
          this.translateX = this.translateX + 50 * this.changeCount;

          // 2,
        } else if (!this.swipeLeftMore) {
          this.translateX = 0;
        } else {
          //,3
          let fdt = this.dateList[0].timestamp;
          let list = [];
          let i;
          for (i = 0; i < this.changeCount; i++) {
            // - 24*(i+1)
            let timestamp = fdt - 1000 * 60 * 60 * 24 * (i + 1);
            let info = this.formatOneDay(new Date(timestamp));
            // ,
            if (this.minDate && info.timestamp < this.minDateTimestamp) {
              this.swipeLeftMore = false;
              break;
            } else {
              list.unshift(info);
            }
          }
          // x
          this.transitionDuration = "0ms";
          this.dateList.unshift(...list);
          // i   i  changeCount 
          this.translateX = this.translateX - 50 * i;

          // div
          setTimeout(() => {
            this.transitionDuration = "300ms";
            this.translateX = this.translateX + 50 * i;
          }, 1);
        }
        // 
      } else if (type === 1) {
        // 
        let hasSpace =
          this.dateList.length * 50 - this.domWidth + this.translateX;

        // 1
        if (hasSpace > 50 * this.changeCount) {
          this.translateX = this.translateX - 50 * this.changeCount;
        } else {
          // 2; 
          if (!this.swipeRightMore) {
            this.translateX = (this.dateList.length - this.visibleDay) * -50;
            return;
          }
          // 3
          let fdt = this.dateList[this.dateList.length - 1].timestamp;
          let i;
          for (i = 0; i < this.changeCount; i++) {
            // + 24*(i+1)
            let timestamp = fdt + 1000 * 60 * 60 * 24 * (i + 1);
            let info = this.formatOneDay(new Date(timestamp));
            // ,
            if (this.maxDate && info.timestamp > this.maxDateTimestamp) {
              this.swipeRightMore = false;
              break;
            } else {
              this.dateList.push(info);
            }
          }
          // ii
          if (i < this.changeCount) {
            this.translateX = (this.dateList.length - this.visibleDay) * -50;
          } else {
            this.translateX = this.translateX - 50 * i;
          }
        }
      }

      /**
       *  
       * function
       */
      setTimeout(() => {
        // 
        let afterChangeX = this.translateX;
        let afterChangeLen = this.dateList.length;
        // x
        if (beforeChangeX === afterChangeX) {
          let changeDay = afterChangeLen - beforeChangeLen;
          // ()
          if (changeDay === 0) return;
          this.firstDay = this.formatOneDay(
            this.formatDateTime(
              this.firstDay.timestamp - 3600 * 1000 * 24 * changeDay
            )
          );
        } else {
          let changeDay = (afterChangeX - beforeChangeX) / 50;
          this.firstDay = this.formatOneDay(
            this.formatDateTime(
              this.firstDay.timestamp - 3600 * 1000 * 24 * changeDay
            )
          );
        }
        // : 
        this.$emit("firstDayChange", this.firstDay);
      }, 300);

      // 
      this.$emit("swipeClick", type===1?'right':'left');
    },
    // 
    formatOneDay(day) {
      let timestamp = new Date(day).getTime();
      let date = this.formatDateTime(timestamp); // 2019/06/01
      let dateArray = date.split("/"); // [2019,06,01]
      // 0
      for (const key in dateArray) {
        if (dateArray[key].indexOf("0") == 0) {
          dateArray[key] = dateArray[key].substr(1, 1);
        }
      }
      return {
        dateFormat: date,
        year: dateArray[0],
        month: dateArray[1],
        date: dateArray[2],
        timestamp: new Date(date).getTime(),
        day: this.getWeekName(new Date(timestamp).getDay())
      };
    },
    // 
    getWeekName(day) {
      const dirt = {
        0: this.sundayText,
        1: "",
        2: "",
        3: "",
        4: "",
        5: "",
        6: ""
      };
      const dirt_en = {
        0: "Su",
        1: "Mo",
        2: "Tu",
        3: "We",
        4: "Th",
        5: "Fr",
        6: "Sa"
      };
      // 
      if (this.lang === "en") {
        return dirt_en[day];
      }
      return dirt[day];
    },
    //  YY/MM/DD 
    formatDateTime(timestamp) {
      if (!timestamp) return "";
      timestamp = parseInt(timestamp); // 
      let fdt = new Date(timestamp);
      let arr = [fdt.getMonth() + 1, fdt.getDate()];
      for (let key in arr) {
        if (arr[key] < 10) {
          arr[key] = "0" + arr[key];
        }
      }
      arr.unshift(fdt.getFullYear());
      return arr[0] + "/" + arr[1] + "/" + arr[2];
    }
  },
  computed: {
    // 0
    minDateTimestamp() {
      if (this.minDate) {
        let day = this.formatOneDay(this.minDate);
        return day.timestamp;
      }
      return null;
    },
    // 0
    maxDateTimestamp() {
      if (this.maxDate) {
        let day = this.formatOneDay(this.maxDate);
        return day.timestamp;
      }
      return null;
    }
  }
};
</script>

<style lang="scss">
.horizontal-calendar {
  width: 100%;
  height: 51px;
  border-bottom: 1px solid #f2f2f2;
  background-color: #fff;
  box-sizing: border-box;
  user-select: none;

  * {
    margin: 0;
    padding: 0;
  }
  div {
    box-sizing: border-box;
  }
  .icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
    font-size: 16px;
    color: #999;
  }

  .date-list {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 60px);
    overflow: hidden;
    position: relative;
  }
  .date-list-scroll {
    width: 60000px;
    height: 50px;
    position: relative;
    transition: transform 0.3s ease-in-out;
    transform: translateX(0);
  }
  .date-item {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 5px 2px;
    line-height: 20px;
    border-right: 1px solid #f2f2f2;
    font-size: 14px;
    text-align: center;
    cursor: pointer;
  }
  .first-day {
    position: absolute;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 90% 90%;
    p {
      color: #fff;
      font-size: 12px;
      width: 54%;
      text-align: center;
      line-height: 16px;
      transform: scale(0.9);
    }
  }
  .date-item-date {
    color: #212121;
    font-size: 15px;
    font-weight: 600;
  }
  .date-item-day {
    color: #999;
    font-size: 13px;
    font-weight: 500;
  }
  .today {
    // background-color: rgba(135, 0, 0, 0.1);
    .date-item-date {
      color: #870000;
    }
  }
  //   
  .choosed-day {
    // background-color: #870000;

    .date-item-date,
    .date-item-day {
      color: #fff;
    }
  }
  .today-not-choosed {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    margin: 6px;
    background-color: rgba(153, 153, 153, 0.3);
    color: #870000;
    font-size: 16px;
    text-align: center;
    line-height: 28px;
    font-weight: 600;
  }

  .calendar {
    display: block;
    width: 54px;
    height: 50px;
    line-height: 50px;
    border-right: 1px solid #f2f2f2;
    font-size: 22px;
    font-weight: 500;
    text-align: center;
    cursor: pointer;
  }
  .left-arrow,
  .right-arrow {
    display: inline-block;
    box-sizing: border-box;
    vertical-align: top;
    width: 30px;
    height: 100%;
    line-height: 50px;
    border-left: 1px solid #f2f2f2;
    border-right: 1px solid #f2f2f2;
    background-color: #f9f9f9;
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    cursor: pointer;
  }
  .month {
    flex: 0 0 19px;
    height: 50px;
    background-color: #aaa;
    color: #fff;
    border-right: 1px solid #f2f2f2;
    line-height: 20px;
    font-size: 12px;
    font-weight: 500;
    text-align: center;
    display: flexbox;
    box-sizing: border-box;
    padding: 5px 0;
  }
}
</style>


